<template>
    <div class="article-list">
        <router-link :to="{name: 'article', query: {id: item._id}}" class="item" v-for="item in content" :key="item._id" >
            <div class="item-top">
                <div class="img-wrap">
                    <img :src="item.author.avatar" >
                </div>
                <div class="item-msg">
                    <div class="row-one">
                        <span class="author-name" >{{item.author.username}}</span>
                        <span class="author-title" >{{item.title}}</span>
                    </div>
                    <div class="row-two">
                        <span class="row-item">浏览:{{item.readNum}}</span>
                        <span class="row-item">回复:{{item.commonNum}}</span>
                        <span class="row-item">分类:{{item.category.name}}</span>
                    </div>
                </div>
                <div class="del flr" >                                    
                    <el-button type="primary" class="del" @click="handleDelete(item._id)">删除</el-button>
                </div>            
            </div>
            <div class="item-content" v-text="item.contentText"></div>
        </router-link>
        <el-pagination class="page" background @current-change="pageChange" :page-size="10" small layout="prev, pager, next"
            :total="count">
        </el-pagination>
    </div>
</template>

<script>
    export default {
        props: {
          articles: Array
        },
        data () {
          return {
            content: [],
            count:0,
            page:3
          }
        },
        methods:{
            getData(){
                this.$axios.get('/article' ).then(res => {
                    console.log(res)
                    this.content = res.data
                    this.count = res.count  
                })
            },
            handleDelete(id){
                console.log(id)
                this.$confirm('此操作将删除该文章, 是否继续?', '警告 ！', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.$axios.delete(`/article/${id}`).then(res => {
                        this.$message.success(res.msg)
                        this.getData()
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            pageChange(page) {
                this.page = page
                this.getData()
            }
        },
        created(){
            this.getData()
        }
    }
</script>

<style scoped>
    .article-list {
        box-sizing: border-box;
        margin-top: 30px;
        border-radius: 4px;
        padding: 2px 20px;
        background: #fff;
    }

    .item {
        text-decoration: none;
        color:#333;
        display: block;
        padding-bottom: 20px;
        margin-top: 20px;
    }
    .item-top {
        display: flex;
    }
    .item-top .img-wrap {
        margin-right: 15px;
        width: 80px;
        height: 80px;  
    }
    .item-top .img-wrap img {
        width: 100%;
        display: block;
        }
    .row-one {
        display: flex;
    }
    .del{
        margin-left: 20px;
        margin-top: 20px;
        opacity: .7;
    }
    .row-one .author-name {
        color: #409eff;
        font-weight: 700;
        padding-right: 8px;
        border-right: 1px solid #000;
    }
    .row-one .author-title {
        font-weight: 700;
        padding-left: 8px;
        font-size: 18px
    }

    .author-name h2 {
        font-size: 18px;
        font-weight: 700;
        margin-left: 15px;
    }

    .item-msg {
        width: 100%;                                                                                                                                                                              
    }
    .row-two {
        box-sizing: border-box;
        padding: 4px 8px;
        margin-top: 15px;
        border-radius: 4px;
        width: 100%;
        background: #bbb;
    }

    .row-two .row-item{
        font-size: 14px;
        color: #333;
        font-weight: 700;
        margin-right: 30px;
    }
</style>


